<template>
  <div class="comment" v-if="comment">
    <div class="top">
      <van-image
        class="header"
        width="40"
        height="40"
        round
        :src="comment.u.p"
        @click.stop="toUser"
      />
      <div class="usermsg">
        <div class="name">
          <p>
            <span>{{ comment.u.n }}</span
            ><i class="lv">LV.{{ comment.u.lvl }}</i>
          </p>
          <div class="like">
            <img
              src="../../assets/like.png"
              v-if="isLike"
              @click="toggleLike"
            />
            <img src="../../assets/unlike.png" v-else @click="toggleLike" />
            <p class="ilike" v-if="isLike">
              {{ comment.like_count == 0 ? 1 : comment.like_count * 1 + 1 }}
            </p>
            <p v-else>
              {{ comment.like_count == 0 ? "赞" : comment.like_count }}
            </p>
          </div>
        </div>
        <div v-if="comment.at" :style="{ color: comment.ac }">
          {{ comment.at }}
        </div>
      </div>
    </div>
    <div class="content">
      <p class="commentContent">{{ comment.content[0].c }}</p>
      <div
        class="otherComments"
        v-show="
          comment.child_comments
            ? comment.child_comments.length != 0
            : comment.child_comments
        "
      >
        <div
          class="otherUser"
          v-for="(item, index) in comment.child_comments"
          :key="index"
        >
          <p>
            <span class="username"
              >{{ item.u.n }}
              <span v-if="author == item.u.id">(作者)</span>
            </span>
            <span v-show="comment.u.id != item.reply_user.id">回复</span>
            <span
              class="username"
              v-show="comment.u.id != item.reply_user.id"
              >{{ item.reply_user.n }}</span
            >
            <span class="usercontent">{{ item.content[0].c }}</span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["author", "comment"],
  data() {
    return {
      isLike: false,
    };
  },
  methods: {
    toggleLike: function () {
      this.isLike = !this.isLike;
    },
    toUser: function () {
      this.$router.push({ path: "/user", query: { id: this.comment.u.id } });
    },
  },
};
</script>

<style lang="less" scoped>
.comment {
  margin: 15px;
  margin-bottom: 25px;
  width: 85%;
  .top {
    display: flex;
    align-items: center;
    .header {
      margin-right: 5px;
    }
    .usermsg {
      flex: 1;
      .name {
        display: flex;
        justify-content: space-between;
        align-items: center;
        p {
          span {
            font-weight: bold;
          }
          .lv {
            font-weight: bold;
            color: #ec8d19;
            display: inline-block;
            transform: scale(0.7);
          }
        }
        .like {
          width: 5vw;
          display: flex;
          justify-content: space-between;
          img {
            width: 20px;
            height: 20px;
            vertical-align: middle;
            margin-right: 5px;
          }
          .ilike {
            color: #e2483c;
          }
        }
      }
    }
  }
  .content {
    margin: 5px;
    margin-left: 50px;
    .commentContent {
    }
    .otherComments {
      background-color: #fafafa;
      padding: 10px;
      border-radius: 5px;
      .otherUser {
        .username {
          margin: 0 8px;
          font-weight: bold;
          &:nth-child(1) {
            margin-left: 0;
          }
        }
        .usercontent::before {
          content: ":";
          font-weight: bold;
        }
      }
    }
  }
}
</style>
