<template>
  <div class="menucard" v-if="list.r" @click="toCook">
    <div
      class="img"
      :style="{
        backgroundImage: `url('${list.r.img}')`,
        backgroundPosition: 'center',
        backgroundSize: 'cover',
      }"
    >
      <img class="icon" v-show="list.r.vfurl" src="../../assets/video.png" />
    </div>
    <p class="view">{{ list.r.recommendation_tag }}</p>
    <p class="name">{{ list.r.n }}</p>
  </div>
</template>

<script>
export default {
  props: ["list"],
  data() {
    return {
      cookId: "",
    };
  },
  mounted() {
    this.cookId = this.list.r.id;
  },
  methods: {
    toCook: function () {
      this.$router.push({ path: "/details", query: { id: this.cookId } });
    },
  },
};
</script>

<style lang="less" scoped>
.menucard {
  .img {
    width: 100vw;
    height: 200px;
    position: relative;
    .icon {
      width: 50px;
      height: 50px;
      position: absolute;
      right: 10px;
      bottom: 10px;
      z-index: 2;
    }
  }
  p {
    margin: 10px 6vw;
    &.view {
      font-weight: bold;
    }
    &.name {
      margin-bottom: 30px;
    }
  }
}
</style>
