<template>
  <div class="basket">
    <div class="top">
      <p class="name">采购清单</p>
      <p class="clear" @click="clear">清空</p>
    </div>
    <van-tabs
      class="tabs"
      v-model="activeName"
      color="#ffb31a"
      padding="0"
      line-width="25px"
      sticky
      offset-top="50px"
      animated
      swipeable
    >
      <van-tab title="按菜谱" name="a"
        ><p class="empty" v-if="basket.length == 0">带着购物单逛超市更方便</p>
        <ul class="cooks" v-else>
          <CookFoods
            v-for="(cookId, index) in basket"
            :key="index"
            :cookId="cookId"
          ></CookFoods>
        </ul>
      </van-tab>
      <van-tab title="全部" name="b"
        ><p class="empty" v-if="basket.length == 0">带着购物单逛超市更方便</p>
        <ul class="foods" v-else>
          <li
            class="foodslis"
            v-for="(foods, index) in allFoods"
            :key="index"
            :foods="foods"
            @click="getIt"
          >
            <p class="foodname">{{ foods.foodsName }}</p>
            <p class="foodquantity">{{ foods.foodsQuantity }}</p>
          </li>
        </ul>
        <ul class="getfoods">
          <li
            class="getFoodslis"
            v-for="(foods, index) in getFoods"
            :key="index + 'get'"
            @click="addIt"
          >
            <p class="foodname">{{ foods.foodsName }}</p>
            <p class="foodquantity">{{ foods.foodsQuantity }}</p>
          </li>
        </ul>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import CookFoods from "@/components/Basket/CookFoods.vue";
import { Dialog } from "vant";

export default {
  components: {
    CookFoods,
  },
  data() {
    return {
      activeName: "a",
      basket: [],
      idArr: [],
      allFoods: [],
      getFoods: [],
    };
  },
  mounted() {
    if (
      JSON.parse(localStorage.getItem("MyBasket")) &&
      JSON.parse(localStorage.getItem("MyBasket")).length != 0
    ) {
      this.basket = JSON.parse(localStorage.getItem("MyBasket"));
    } else {
      this.clearBasket();
    }
    if (JSON.parse(localStorage.getItem("getFoods"))) {
      this.getFoods = JSON.parse(localStorage.getItem("getFoods"));
    }
    if (JSON.parse(localStorage.getItem("allFoods"))) {
      this.allFoods = JSON.parse(localStorage.getItem("allFoods"));
      this.idArr = JSON.parse(localStorage.getItem("idArr"));
      this.basket.map((id) => {
        if (this.idArr.indexOf(id) == -1) {
          this.idArr.push(id);
          localStorage.setItem("idArr", JSON.stringify(this.idArr));
          this.axios
            .get("https://apis.netstart.cn/douguo/recipe/detail/" + id + "")
            .then((res) => {
              res.data.result.recipe.major.map((item) => {
                this.allFoods.push({
                  foodsName: item.title,
                  foodsQuantity: item.note,
                });
              });
              localStorage.setItem("allFoods", JSON.stringify(this.allFoods));
            });
        }
      });
    } else {
      this.basket.map((id) => {
        this.idArr.push(id);
        localStorage.setItem("idArr", JSON.stringify(this.idArr));
        this.axios
          .get("https://apis.netstart.cn/douguo/recipe/detail/" + id + "")
          .then((res) => {
            res.data.result.recipe.major.map((item) => {
              this.allFoods.push({
                foodsName: item.title,
                foodsQuantity: item.note,
              });
            });
            localStorage.setItem("allFoods", JSON.stringify(this.allFoods));
          });
      });
    }
  },
  methods: {
    clear: function () {
      Dialog.confirm({
        title: "提示",
        message: "确认清空吗?",
        confirmButtonText: "是",
        confirmButtonColor: "#69a5ad",
        cancelButtonText: "否",
        cancelButtonColor: "#c4c4c4",
      })
        .then(() => {
          this.clearBasket();
        })
        .catch(() => {
          return;
        });
    },
    clearBasket: function () {
      this.basket = [];
      localStorage.removeItem("MyBasket");
      localStorage.removeItem("allFoods");
      localStorage.removeItem("getFoods");
      localStorage.removeItem("idArr");
      localStorage.removeItem("foodsArr");
    },
    sort: function (arr) {
      arr.sort(sortBy("foodsName", 1));
      //attr：根据该属性排序；rev：升序1或降序-1，不填则默认为1
      function sortBy(attr, rev) {
        if (rev == undefined) {
          rev = 1;
        } else {
          rev ? 1 : -1;
        }
        return function (a, b) {
          a = a[attr];
          b = b[attr];
          if (a < b) {
            return rev * -1;
          }
          if (a > b) {
            return rev * 1;
          }
          return 0;
        };
      }
    },
    getIt: function (e) {
      var node;
      if (e.target.nodeName != "LI") {
        node = e.target.parentNode;
      } else {
        node = e.target;
      }
      var arr = [];
      arr.push(...node.parentNode.childNodes);
      var n = arr.indexOf(node);
      this.getFoods.unshift(...this.allFoods.splice(n, 1));
      localStorage.setItem("getFoods", JSON.stringify(this.getFoods));
      localStorage.setItem("allFoods", JSON.stringify(this.allFoods));
    },
    addIt: function (e) {
      var node;
      if (e.target.nodeName != "LI") {
        node = e.target.parentNode;
      } else {
        node = e.target;
      }
      var arr = [];
      arr.push(...node.parentNode.childNodes);
      var n = arr.indexOf(node);
      this.allFoods.push(...this.getFoods.splice(n, 1));
      this.sort(this.allFoods);
      localStorage.setItem("allFoods", JSON.stringify(this.allFoods));
      localStorage.setItem("getFoods", JSON.stringify(this.getFoods));
    },
  },
};
</script>

