<template>
  <div class="menu" v-if="menuList.length != 0">
    <MenuCard
      v-for="(list, index) in menuList"
      :key="index"
      :list="list"
    ></MenuCard>
  </div>
</template>

<script>
import MenuCard from "@/components/User/MenuCard.vue";

export default {
  props: ["userId"],
  components: {
    MenuCard,
  },
  data() {
    return {
      menuList: [],
    };
  },
  mounted() {
    this.axios
      .get(
        "https://apis.netstart.cn/douguo/user/urecipes/" + this.userId + "/0/20"
      )
      .then((res) => {
        this.menuList = res.data.result.list;
      });
  },
};
</script>

<style lang="less" scoped></style>
