import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    searchlastrouter: "",
    classifylastrouter: "",
    classifysearch: "",
    favList: [],
    lists: [],
    login: false,
    loginstate: null,

  },
  mutations: {
    savesearchrouter(state, str) {
      state.searchlastrouter = str
    },
    saveclassifyrouter(state, str) {
      state.classifylastrouter = str
    },
    classifytosearch(state, str) {
      state.classifysearch = str
    },

    addCar(state, str) {
      let CarCon = state.lists;
      let isHas = CarCon.some((item) => {
        if (str.id == item.id) {
          item.num++;
          return true;
        } else {
          return false;
        }
      })

      if (!isHas) {
        let obj = {
          "id": str.id,
          "title": str.title,
        }
        this.state.lists.push(obj)
      }
    },

    deleteCar(state, str) {
      let len = state.lists.length;
      for (var i = 0; i < len; i++) {
        if (state.lists[i].title == str.title) {
          state.lists.splice(i, 1);
          break;
        }
      }
    },

    initFav(state) {
      if (JSON.parse(localStorage.getItem('fav'))) {
        state.favList = JSON.parse(localStorage.getItem('fav'));
      }
    },

    addfav(state, id) {
      state.favList.push(id);
      localStorage.setItem("fav", JSON.stringify(state.favList));
    },

    cutfav(state, id) {
      let n = state.favList.findIndex(ele => ele == id);
      state.favList.splice(n - 1, 1);
      localStorage.setItem("fav", JSON.stringify(state.favList));
    },

    tologin(state, [name, password]) {
      state.loginstate = null;
      console.log(name, password);
      let n = state.user.find(ele => ele.name == name);
      console.log(n);
      if (n == undefined || n.password != password) {
        state.loginstate = false;
      } else {
        window.localStorage.setItem("login", true);
        state.loginstate = true;
      }
    },
  },
  actions: {
    addCar({ commit }, str) {

      setTimeout(function () {
        let result = 'ok'
        if (result == 'ok') {
          // 提交给mutations
          commit("addCar", str)
        }
      }, 100)
    },

    deleteCar({ commit }, str) {

      setTimeout(function () {
        let result = 'ok'
        if (result == 'ok') {
          // 提交给mutations
          commit("deleteCar", str)
        }
      }, 100)
    },
  },
  modules: {}
})