<template>
  <div class="Admonistrationcard" v-if="cookList && cookId">
    <img
      class="check-icon"
      v-if="isTick"
      src="../../assets/checked.png"
      @click="toggleTick"
    />
    <img
      class="check-icon"
      v-else
      src="../../assets/unchecked.png"
      @click="toggleTick"
    />
    <div
      class="img"
      v-if="cookList.photo_path"
      :style="{
        backgroundImage: 'url(' + cookList.photo_path + ')',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
      }"
    >
      <span class="icon" v-if="cookList.vfurl"></span>
    </div>
    <p class="sketch">
      <span class="title">{{ cookList.title }}</span>
      <span class="look-learn">
        <span v-if="cookList.views_count_text"
          >{{ cookList.views_count_text }}人看过</span
        >
      </span>
      <span class="author">
        <van-image
          width="20"
          height="20"
          round
          :src="cookList.user.user_photo"
        />
        <span class="name">{{ cookList.user.nick }}</span>
      </span>
    </p>
  </div>
</template>

<script>
export default {
  props: ["cookId", "checkCooks"],
  data() {
    return {
      cookList: "",
      isTick: false,
    };
  },
  mounted() {
    this.axios
      .get("https://apis.netstart.cn/douguo/recipe/detail/" + this.cookId + "")
      .then((res) => {
        this.cookList = res.data.result.recipe;
      });
  },
  methods: {
    toggleTick: function () {
      this.isTick = !this.isTick;
      var index = this.checkCooks.indexOf(this.cookId);
      if (index == -1) {
        this.checkCooks.push(this.cookId);
      } else {
        this.checkCooks.splice(index, 1);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.Admonistrationcard {
  padding: 10px 20px;
  width: 100vw;
  display: flex;
  align-items: center;
  .check-icon {
    width: 25px;
    margin-right: 15px;
  }
  .img {
    flex: 1;
    height: 120px;
    border-radius: 10px;
    position: relative;
    .icon {
      width: 50px;
      height: 50px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: url("../../assets/video.png");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }
  }
  .sketch {
    flex: 1;
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    & > span {
      margin: 2px 0;
    }
    .title {
      font-weight: bold;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    .look-learn {
      font-size: 14px;
      .dot {
        font-weight: bold;
      }
    }
    .author {
      display: flex;
      align-items: center;
      margin-top: 10px;
      .name {
        font-size: 14px;
        margin-left: 5px;
        color: #c4c4c4;
      }
    }
  }
}
</style>
