<template>
  <div class="recommend">
    <div class="msg_list">
      <Refresh :onRefresh="onRefresh">
        <van-swipe :autoplay="3000">
          <van-swipe-item v-for="(image, index) in images" :key="index">
            <img v-lazy="image" />
          </van-swipe-item>
        </van-swipe>
        <RecommendCard
          v-for="(list, index) in cardMsgs"
          :key="index"
          :list="list"
        ></RecommendCard>
        <div class="loading">
          <img src="../../assets/loading2.gif" />
        </div>
      </Refresh>
    </div>
  </div>
</template>

<script>
import Refresh from "@/components/Refresh.vue";
import RecommendCard from "@/components/Home/RecommendCard.vue";

export default {
  components: {
    Refresh,
    RecommendCard,
  },
  data() {
    return {
      images: [],
      cardMsgs: [],
      count: 0,
      loading: false,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onLoad, true);
    this.count = Math.floor(Math.random() * 100) * 10;
    this.axios
      .get(`https://apis.netstart.cn/douguo/home/recommended/${this.count}/10`)
      .then((res) => {
        this.images = res.data.result.banner.map((item) => item.i);
        this.cardMsgs = res.data.result.list.filter((item) => {
          return item.type !== 128;
        });
      });
  },
  methods: {
    onRefresh(done) {
      this.cardMsgs = [];
      this.count = Math.floor(Math.random() * 100) * 10;
      this.axios
        .get(
          `https://apis.netstart.cn/douguo/home/recommended/${this.count}/10`
        )
        .then((res) => {
          this.cardMsgs = res.data.result.list.filter((item) => {
            return item.type !== 128;
          });
        });
      done();
    },
    onLoad() {
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      let clientHeight = document.documentElement.clientHeight;
      let scrollHeight = document.documentElement.scrollHeight;
      if (scrollTop + clientHeight >= scrollHeight - 10 && !this.loading) {
        this.loading = true;
        setTimeout(() => {
          this.count += 10;
          this.axios
            .get(
              `https://apis.netstart.cn/douguo/home/recommended/${this.count}/10`
            )
            .then((res) => {
              var idArr = [];
              var MidMsgs = [];
              this.cardMsgs.map((item) => {
                if (item.r) {
                  if (item.type == 1) {
                    idArr.push(item.r.id);
                  }
                }
              });
              MidMsgs = res.data.result.list.filter((item) => {
                return item.type !== 128;
              });
              MidMsgs.map((item) => {
                if (item.r) {
                  if (idArr.indexOf(item.r.id) == -1) {
                    this.cardMsgs.push(item);
                  } else {
                    return;
                  }
                }
              });
            });
          this.loading = false;
        }, 2000);
      }
    },
  },
  destroyed() {
    window.removeEventListener("scroll", this.onLoad, false);
  },
};
</script>

<style lang="less" scoped>
.recommend {
  width: 100vw;
  height: 100vh;
  overflow: auto;
  margin-top: 100px;
  .msg_list {
    /deep/ .van-swipe__indicator {
      width: 12px;
      height: 5px;
      transform: scaleY(0.5);
      border-radius: 20%;
      background-color: #2c3e50;
    }
    /deep/ .van-swipe__indicator--active {
      background-color: #efe;
    }
    .loading {
      img {
        width: 20vw;
        display: block;
        margin: 0 auto;
        margin-bottom: 50px;
      }
    }
  }
}
</style>
