<template>
  <div class="collection">
    <CollectionCard
      v-for="(list, index) in collectionList"
      :key="index"
      :list="list"
    ></CollectionCard>
  </div>
</template>

<script>
import CollectionCard from "@/components/User/CollectionCard.vue";

export default {
  props: ["userId"],
  components: {
    CollectionCard,
  },
  data() {
    return {
      collectionList: [],
    };
  },
  mounted() {
    this.axios
      .get(
        `https://apis.netstart.cn/douguo/user/ufavorites/${this.userId}/0/20`
      )
      .then((res) => {
        this.collectionList = res.data.result.recipes;
      });
  },
};
</script>

<style lang="less" scoped></style>
