<template>
  <div class="note-details" v-if="noteDetails">
    <van-image-preview
      v-model="topImageShow"
      :images="topImage"
      :show-index="false"
    >
    </van-image-preview>
    <van-image-preview
      v-model="stepImageShow"
      :images="stepImages"
      :start-position="stepImagesIndex"
      :closeable="true"
      close-icon="arrow-left"
      closeIconPosition="top-left"
    >
      <template v-slot:stepContents>{{ stepContents }}</template>
    </van-image-preview>
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      @select="onSelect"
    />
    <div class="topbar" v-if="!scrollLeaveTop">
      <img class="icon" src="../../assets/back_white.png" @click="back" />
      <img
        class="icon"
        src="../../assets/menu_more_white.png"
        @click="showShare = true"
      />
    </div>
    <div class="topbar-white" v-else>
      <div class="left">
        <img class="icon" src="../../assets/back_black.png" @click="back" />
        <div class="hideuser" v-show="hideShow">
          <img :src="noteDetails.user.user_photo" @click.stop="toUser" />
          <div class="unfocus" v-if="!isFollow" @click.stop="addFollowThis">
            关注
          </div>
          <div class="focus" v-else @click.stop="removeFollowThis">已关注</div>
          <span class="unfav" v-if="!isFav" @click.stop="attentionThis"></span>
          <span class="fav" v-else @click.stop="cancelThis"></span>
        </div>
      </div>
      <img
        class="icon"
        src="../../assets/menu_more_black.png"
        @click="showShare = true"
      />
    </div>
    <div class="mask" v-show="isShowMask"></div>
    <div class="content">
      <img
        :src="noteDetails.photo_path"
        @click="toggleTopImg"
        v-if="!noteDetails.vfurl"
        :onload="loaded()"
      />
      <video
        v-else
        :src="noteDetails.vfurl"
        controls
        muted
        style="width: 100%"
        :onload="loaded()"
      ></video>
      <div class="container">
        <p class="title">{{ noteDetails.title }}</p>
        <span class="s-title">
          <span v-if="noteDetails.hq">精品</span>
          <span v-if="noteDetails.ecs">独家</span>
          <span v-if="noteDetails.views_count_text">
            浏览{{ noteDetails.views_count_text }}
          </span>
          <span v-if="noteDetails.favo_counts_text">
            收藏{{ noteDetails.favo_counts_text }}
          </span>
          <span v-if="noteDetails.dish_count_text">
            学做{{ noteDetails.dish_count_text }}
          </span>
        </span>
        <div class="user" ref="user">
          <div class="username" v-if="noteDetails.user" @click.stop="toUser">
            <img :src="noteDetails.user.user_photo" />
            <span class="name">{{ noteDetails.user.nickname }}</span>
            <i class="level">Lv.{{ noteDetails.user.lvl }}</i>
          </div>
          <div class="ff">
            <span
              class="unfav"
              v-if="!isFav"
              @click.stop="attentionThis"
            ></span>
            <span class="fav" v-else @click.stop="cancelThis"></span>
            <div class="unfollow" v-if="!isFollow" @click="addFollowThis">
              关注
            </div>
            <div class="follow" v-else @click="removeFollowThis">已关注</div>
          </div>
        </div>
        <div class="detail">
          <p v-for="(item, index) in cookstorys" :key="index" v-html="item"></p>
        </div>
        <div
          class="tal"
          v-if="noteDetails.cook_time && noteDetails.cook_difficulty"
        >
          <div class="time">
            <img :src="noteDetails.cook_time_image" />
            <span>{{ noteDetails.cook_time }}</span>
          </div>
          <div class="lv">
            <img :src="noteDetails.cook_difficulty_image" />
            <span>{{ noteDetails.cook_difficulty }}</span>
          </div>
        </div>
        <div class="major">
          <span class="list-top">
            <span>食材清单</span>
            <span v-if="!isInMyBasket" @click="addInMyBasket"
              >加入采购清单</span
            >
            <span v-else @click="removeInMyBasket">移出采购清单</span>
          </span>
          <p
            class="foods"
            v-for="(item, index) in noteDetails.major"
            :key="index"
          >
            <span class="isClick" v-if="item.tu" @click="showFoodsAnalysis">{{
              item.title
            }}</span>
            <span v-else>{{ item.title }}</span>
            <span>{{ item.note }}</span>
          </p>
        </div>
        <div class="nutrition" v-if="noteDetails.nutrition_facts != ''">
          <div class="nut-top">
            <span>营养分析</span>
            <span @click="showNutritionalAnalysis">查看详情</span>
          </div>
          <div class="analysis">
            <div class="canvas">
              <canvas
                ref="canvas"
                width="50"
                height="50"
                style="transform: rotate(-90deg)"
              ></canvas>
              <div class="nutrients">
                <div v-for="(item, index) in facts" :key="index">
                  <span
                    class="dote"
                    :style="{ background: item.pie_chart_color }"
                  ></span>
                  <span>{{ item.nutrient }}</span>
                  <span>{{ item.amount }}</span>
                </div>
              </div>
            </div>
            <div class="energy">
              <p>{{ noteDetails.energy }}</p>
              <span class="dw">千卡</span>
            </div>
          </div>
          <div class="nut-bottom">
            {{ noteDetails.nutrition_facts_hint_text }}
          </div>
        </div>
        <div class="steps">
          <div class="step-top">
            <span>烹饪步骤</span>
            <span @click="toggleStepImg">点击图片进入烹饪模式</span>
          </div>
          <div class="everystep">
            <div class="step" v-for="(item, index) in steps" :key="index">
              <span v-if="isLast(item.position)">
                步骤{{ item.position }}/{{ steps.length }}
              </span>
              <span v-else>最后一步</span>
              <img
                :src="item.image"
                :data-index="index"
                @click="toggleStepImg"
              />
              <p class="content">{{ item.content }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="comment" v-if="comments.length != 0">
      <p class="title">
        热门评论
        <span @click="commentShow = true"
          >{{ noteDetails.comments_count_text }}条评论</span
        >
      </p>
      <Comment
        v-for="(comment, index) in threeComments"
        :key="index"
        :author="author"
        :comment="comment"
      ></Comment>
      <p
        class="allcomment"
        @click="commentShow = true"
        v-if="threeComments.length >= 3"
      >
        查看全部{{ noteDetails.comments_count_text }}条评论
      </p>
      <van-action-sheet
        v-model="commentShow"
        :title="'全部评论(' + noteDetails.comments_count_text + ')'"
      >
        <Comment
          v-for="(comment, index) in comments"
          :key="index"
          :author="author"
          :comment="comment"
        ></Comment>
      </van-action-sheet>
    </div>

    <div class="foods-analysis" v-show="isFoodsAnalysisShow">
      <div class="foods-analysis-top">
        <img src="../../assets/back_black.png" @click="closeFoodsAnalysis" />
        <span>{{ foodName }}的食材百科</span>
      </div>
      <iframe
        id="foods-tempHtml"
        :src="foodUrl"
        frameborder="0"
        v-show="isShowFoodsContent"
      ></iframe>
      <div class="bg">
        <div class="loadingBar"></div>
      </div>
    </div>
    <div class="nutritional-analysis" v-show="isNutritionalAnalysisShow">
      <div class="nutritional-analysis-top">
        <img
          src="../../assets/back_black.png"
          @click="closeNutritionalAnalysis"
        />
        <span>详细营养分析</span>
      </div>
      <iframe
        id="nutritional-tempHtml"
        frameborder="0"
        :src="nutritionUrl"
        v-show="isShowNutritionalContent"
      ></iframe>
      <div class="bg">
        <div class="loadingBar"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import Comment from "@/components/Home/Comment.vue";

export default {
  components: {
    Comment,
  },
  data() {
    return {
      id: "",
      author: "",
      peopleId: "",
      noteDetails: [],
      isShowMask: true,
      cookstorys: [],
      facts: [],
      steps: [],
      topImage: [],
      topImageShow: false,
      isFav: false,
      isFollow: false,
      stepImages: [],
      stepImageShow: false,
      stepImagesIndex: 0,
      stepContents: [],
      isInMyBasket: false,
      showShare: false,
      options: [
        [
          { name: "微信", icon: "wechat" },
          { name: "朋友圈", icon: "wechat-moments" },
          { name: "微博", icon: "weibo" },
          { name: "QQ", icon: "qq" },
        ],
        [
          { name: "复制链接", icon: "link" },
          { name: "分享海报", icon: "poster" },
          { name: "二维码", icon: "qrcode" },
          { name: "小程序码", icon: "weapp-qrcode" },
        ],
      ],
      topPhotoHeight: "",
      userHeight: "",
      scrollLeaveTop: false,
      hideShow: false,
      ifeData: "",
      isNutritionalAnalysisShow: false,
      isShowNutritionalContent: false,
      isFoodsAnalysisShow: false,
      foodName: "",
      isShowFoodsContent: false,
      comments: [],
      threeComments: [],
      commentShow: false,
      foodUrl: "",
      nutritionUrl: "",
      foodLi: [],
      foodsLis: [],
      foodsArr: [],
    };
  },
  mounted() {
    window.addEventListener("scroll", this.windowScroll, true);
    this.id = this.$route.query.id;
    if (this.id != "") {
      this.axios
        .get("https://apis.netstart.cn/douguo/recipe/detail/" + this.id + "")
        .then((res) => {
          this.noteDetails = res.data.result.recipe;
          this.author = this.noteDetails.user.user_id;
          this.cookstorys = this.noteDetails.cookstory.split("\n");
          this.topImage.push(this.noteDetails.photo_path);
          this.stepImages = this.noteDetails.cookstep.map((item) => item.image);
          this.stepContents = this.noteDetails.cookstep.map(
            (item) => item.content
          );
          this.facts = res.data.result.recipe.nutrition_facts;
          this.steps = res.data.result.recipe.cookstep;
          this.topPhotoHeight =
            (document.body.clientWidth / this.noteDetails.pw) *
            this.noteDetails.ph;
          this.userHeight = this.$refs.user.offsetTop + this.topPhotoHeight;
          this.peopleId = this.noteDetails.user.user_id;
          if (JSON.parse(localStorage.getItem("FollowPeoples"))) {
            var followArr = JSON.parse(localStorage.getItem("FollowPeoples"));
            if (followArr.indexOf(this.peopleId) != -1) {
              this.isFollow = true;
            }
          }
        });
      this.nutritionUrl =
        "https://apis.netstart.cn/douguo/recipe/nutrient/" + this.id + "";
      var i = 0;
      this.axios
        .get(
          "https://apis.netstart.cn/douguo/recipe/flatcomments/" +
            this.id +
            "/0/20"
        )
        .then((res) => {
          this.comments = res.data.result.comments;
          var n;
          if (this.comments.length > 3) {
            n = 3;
          } else {
            n = this.comments.length;
          }
          while (this.threeComments.length < n) {
            this.threeComments.push(this.comments[i]);
            i++;
          }
        });
      this.$toast({
        message: "加载中...",
        icon: require("../../assets/loading5.gif"),
        forbidClick: true,
        overlay: true,
      });
      if (JSON.parse(localStorage.getItem("favCooks"))) {
        var idArr = JSON.parse(localStorage.getItem("favCooks"));
        var index = idArr.indexOf(this.id);
        if (index != -1) {
          this.isFav = true;
        } else {
          index = idArr.indexOf(this.id * 1);
          if (index != -1) {
            this.isFav = true;
          }
        }
      }
    }
    if (JSON.parse(localStorage.getItem("MyBasket"))) {
      var basketArr = JSON.parse(localStorage.getItem("MyBasket"));
      if (basketArr.indexOf(this.id) != -1) {
        this.isInMyBasket = true;
      }
    }
  },
  methods: {
    loaded: function () {
      setTimeout(() => {
        this.isShowMask = false;
      }, 2000);
    },
    back: function () {
      this.$router.back();
    },
    isLast: function (num) {
      if (num !== this.steps.length) {
        return true;
      } else {
        return false;
      }
    },
    windowScroll(e) {
      if (!this.commentShow) {
        if (e.target.scrollTop >= this.topPhotoHeight / 2) {
          this.scrollLeaveTop = true;
        } else {
          this.scrollLeaveTop = false;
        }
        if (e.target.scrollTop >= this.userHeight) {
          this.hideShow = true;
        } else {
          this.hideShow = false;
        }
      }
    },
    toggleTopImg: function () {
      this.topImageShow = true;
    },
    toggleStepImg: function (e) {
      this.stepImageShow = true;
      this.stepImagesIndex = e.target.getAttribute("data-index") * 1;
    },
    onSelect(option) {
      Toast(option.name);
      this.showShare = false;
    },
    showNutritionalAnalysis: function () {
      this.isNutritionalAnalysisShow = !this.isNutritionalAnalysisShow;
      setTimeout(() => {
        this.isShowNutritionalContent = !this.isShowNutritionalContent;
      }, 1000);
    },
    closeNutritionalAnalysis: function () {
      this.isNutritionalAnalysisShow = !this.isNutritionalAnalysisShow;
      this.isShowNutritionalContent = !this.isShowNutritionalContent;
    },
    showFoodsAnalysis: function (e) {
      this.foodName = e.target.innerText;
      this.isFoodsAnalysisShow = !this.isFoodsAnalysisShow;
      setTimeout(() => {
        this.isShowFoodsContent = !this.isShowFoodsContent;
      }, 1000);
    },
    closeFoodsAnalysis: function () {
      this.isFoodsAnalysisShow = !this.isFoodsAnalysisShow;
      this.isShowFoodsContent = !this.isShowFoodsContent;
    },
    addFollowThis: function () {
      this.isFollow = true;
      var idArr = [];
      if (JSON.parse(localStorage.getItem("FollowPeoples"))) {
        idArr = JSON.parse(localStorage.getItem("FollowPeoples"));
      }
      if (idArr.indexOf(this.peopleId) == -1) {
        idArr.unshift(this.peopleId);
        localStorage.setItem("FollowPeoples", JSON.stringify(idArr));
      }
    },
    removeFollowThis: function () {
      this.isFollow = false;
      var idArr = [];
      idArr = JSON.parse(localStorage.getItem("FollowPeoples"));
      idArr.splice(idArr.indexOf(this.peopleId), 1);
      localStorage.setItem("FollowPeoples", JSON.stringify(idArr));
    },
    attentionThis: function () {
      this.isFav = true;
      var idArr = [];
      if (JSON.parse(localStorage.getItem("favCooks"))) {
        idArr = JSON.parse(localStorage.getItem("favCooks"));
      }
      if (idArr.indexOf(this.id) == -1) {
        idArr.unshift(this.id);
        localStorage.setItem("favCooks", JSON.stringify(idArr));
      }
    },
    cancelThis: function () {
      this.isFav = false;
      var idArr = [];
      idArr = JSON.parse(localStorage.getItem("favCooks"));
      idArr.splice(idArr.indexOf(this.cookId), 1);
      localStorage.setItem("favCooks", JSON.stringify(idArr));
    },
    addInMyBasket: function () {
      Toast("已添加至采购清单");
      this.isInMyBasket = true;
      var idArr = [];
      if (JSON.parse(localStorage.getItem("MyBasket"))) {
        idArr = JSON.parse(localStorage.getItem("MyBasket"));
      }
      if (idArr.indexOf(this.id) == -1) {
        idArr.unshift(this.id);
        localStorage.setItem("MyBasket", JSON.stringify(idArr));
      }
      if (JSON.parse(localStorage.getItem("foodsArr"))) {
        this.foodsArr = JSON.parse(localStorage.getItem("foodsArr"));
        var foodsIdArr = this.foodsArr.map((item) => {
          return item.id;
        });
        if (foodsIdArr.indexOf(this.id) == -1) {
          foodsIdArr.push(this.id);
          this.axios
            .get(
              "https://apis.netstart.cn/douguo/recipe/detail/" + this.id + ""
            )
            .then((res) => {
              res.data.result.recipe.major.map((item) => {
                this.foodsLis.push({
                  foodsName: item.title,
                  foodsQuantity: item.note,
                  isGet: false,
                });
              });
              this.foodsArr.unshift({ id: this.id, foodsLis: this.foodsLis });
              localStorage.setItem("foodsArr", JSON.stringify(this.foodsArr));
            });
        }
      } else {
        this.axios
          .get("https://apis.netstart.cn/douguo/recipe/detail/" + this.id + "")
          .then((res) => {
            res.data.result.recipe.major.map((item) => {
              this.foodsLis.push({
                foodsName: item.title,
                foodsQuantity: item.note,
                isGet: false,
              });
            });
            this.foodsArr.unshift({ id: this.id, foodsLis: this.foodsLis });
            localStorage.setItem("foodsArr", JSON.stringify(this.foodsArr));
          });
      }
    },
    removeInMyBasket: function () {
      this.isInMyBasket = false;
      var idArr = [];
      idArr = JSON.parse(localStorage.getItem("MyBasket"));
      idArr.splice(idArr.indexOf(this.id), 1);
      localStorage.setItem("MyBasket", JSON.stringify(idArr));
      this.foodsArr = JSON.parse(localStorage.getItem("foodsArr"));
      var foodsIdArr = this.foodsArr.map((item) => {
        return item.id;
      });
      var n = foodsIdArr.indexOf(this.id);
      this.foodsArr.splice(n, 1);
      localStorage.setItem("foodsArr", JSON.stringify(this.foodsArr));
    },
    toUser: function () {
      this.$router.push({ path: "/user", query: { id: this.author } });
    },
  },
  watch: {
    facts: function (n) {
      if (this.facts.length != 0) {
        var context = this.$refs.canvas.getContext("2d");
        context.lineWidth = 13;

        context.beginPath();
        context.arc(
          25,
          25,
          15,
          (Math.PI / 180) * 0,
          (Math.PI / 180) * 360 * (n[0].calorie / this.noteDetails.energy)
        );
        context.strokeStyle = n[0].pie_chart_color;
        context.stroke();
        context.closePath();

        context.beginPath();
        context.arc(
          25,
          25,
          15,
          (Math.PI / 180) * 360 * (n[0].calorie / this.noteDetails.energy),
          (Math.PI / 180) *
            360 *
            ((n[0].calorie + n[1].calorie) / this.noteDetails.energy)
        );
        context.strokeStyle = n[1].pie_chart_color;
        context.stroke();
        context.closePath();

        context.beginPath();
        context.arc(
          25,
          25,
          15,
          (Math.PI / 180) *
            360 *
            ((n[0].calorie + n[1].calorie) / this.noteDetails.energy),
          (Math.PI / 180) * 0
        );
        context.strokeStyle = n[2].pie_chart_color;
        context.stroke();
        context.closePath();
      }
    },
    foodName: function () {
      if (this.foodName != "") {
        this.foodUrl =
          "https://apis.netstart.cn/douguo/recipe/material/" + this.foodName;
      }
    },
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.windowScroll, false);
  },
};
</script>

<style lang="less" scoped>
.note-details {
  width: 100vw;
  height: 100%;
  position: absolute;
  z-index: 2;
  background-color: #fff;
  overflow-y: auto;
  .topbar {
    width: 100%;
    height: 70px;
    background: linear-gradient(to bottom, #000, transparent);
    position: fixed;
    top: 0;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    .icon {
      width: 20px;
      height: 20px;
    }
  }
  .topbar-white {
    width: 100%;
    height: 70px;
    background: #fff;
    position: fixed;
    top: 0;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    .icon {
      width: 20px;
      height: 20px;
    }
    .left {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .hideuser {
        display: flex;
        align-items: center;
        img {
          width: 30px;
          margin-right: 10px;
          border-radius: 50%;
          vertical-align: middle;
        }
        .unfocus {
          margin-right: 5px;
          font-size: 12px;
          font-weight: bold;
          padding: 5px 15px;
          border-radius: 15px;
          background-color: #ffc533;
        }
        .focus {
          margin-right: 5px;
          font-size: 12px;
          font-weight: bold;
          padding: 5px 9px;
          border-radius: 15px;
          background-color: #f5f5f5;
        }
        span {
          width: 5vw;
          height: 5vw;
          margin-left: 10px;
          display: block;
          &.unfav {
            background: url("../../assets/unfavorite.png");
            background-size: cover;
            background-position: center;
          }
          &.fav {
            background: url("../../assets/favorite.png");
            background-size: cover;
            background-position: center;
          }
        }
      }
    }
  }
  .mask {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: 2;
  }
  .content {
    .container {
      margin: 15px;
      .title {
        font-weight: bold;
        font-size: 22px;
      }
      .s-title {
        margin-top: 5px;
        display: inline-block;
        span {
          &::after {
            content: " • ";
          }
          &:last-child::after {
            content: "";
          }
        }
      }
      .user {
        margin-top: 30px;
        font-size: 12px;
        font-weight: bold;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .username {
          display: flex;
          align-items: center;
          img {
            width: 30px;
            margin-right: 3px;
            border-radius: 50%;
          }
          .level {
            font-size: 14px;
            color: #ec8d19;
            display: inline-block;
            transform: scale(0.7);
          }
        }
        .ff {
          display: flex;
          span {
            width: 5vw;
            height: 5vw;
            margin-right: 20px;
            display: block;
            &.unfav {
              background: url("../../assets/unfavorite.png");
              background-size: cover;
              background-position: center;
            }
            &.fav {
              background: url("../../assets/favorite.png");
              background-size: cover;
              background-position: center;
            }
          }
          .unfollow {
            margin-right: 5px;
            font-size: 12px;
            font-weight: bold;
            padding: 3px 15px;
            border-radius: 15px;
            background-color: #ffc533;
          }
          .follow {
