<template>
  <div class="searchcook">
    <div class="top">
      <div class="classification">
        <div class="left">
          <p :class="{ active: activeOrder == 0 }" @click="toggleActive(0)">
            综合
          </p>
          <p :class="{ active: activeOrder == 2 }" @click="toggleActive(2)">
            收藏高
          </p>
          <p :class="{ active: activeOrder == 3 }" @click="toggleActive(3)">
            学做多
          </p>
        </div>
        <!-- <div class="right">
          <p>视频</p>
        </div> -->
      </div>
      <div class="second" v-if="secondList.length != 0">
        <span
          :class="{ active: activeSecond == word }"
          v-for="(word, index) in secondList"
          :key="index"
          @click="toggleSecond(word)"
          >{{ word }}</span
        >
      </div>
    </div>
    <div class="content">
      <SearchCard
        v-for="(list, index) in searchList"
        :key="index"
        :list="list"
      ></SearchCard>
    </div>
    <div class="loading">
      <img src="../../assets/loading2.gif" />
    </div>
  </div>
</template>

<script>
import SearchCard from "@/components/Search/SearchCard.vue";

export default {
  props: ["value"],
  components: {
    SearchCard,
  },
  data() {
    return {
      activeOrder: 0,
      activeSecond: "",
      searchList: [],
      secondList: [],
      count: 0,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onLoad, true);
    this.axios
      .get(
        "https://apis.netstart.cn/douguo/recipe/search?keyword=" +
          this.value +
          ""
      )
      .then((res) => {
        res.data.result.list.map((item) => {
          if (item.type == 13) {
            this.searchList.push(item);
          }
        });
        this.secondList = res.data.result.secondary_keywords;
        this.activeSecond = this.secondList[0];
      });
  },
  methods: {
    toggleActive: function (n) {
      this.activeOrder = n;
      this.searchList = [];
      this.axios
        .get(
          "https://apis.netstart.cn/douguo/recipe/search?keyword=" +
            this.value +
            "&order=" +
            this.activeOrder +
            "&type=0&offset=0&limit=20"
        )
        .then((res) => {
          this.searchList = res.data.result.list;
        });
    },
    toggleSecond: function (n) {
      this.activeSecond = n;
      this.searchList = [];
      this.axios
        .get(
          "https://apis.netstart.cn/douguo/recipe/search?keyword=" +
            this.value +
            "&order=" +
            this.activeOrder +
            "&type=0&secondary_keyword=" +
            this.activeSecond +
            "&offset=0&limit=20"
        )
        .then((res) => {
          this.searchList = res.data.result.list;
        });
    },
    onLoad() {
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      let clientHeight = document.documentElement.clientHeight;
      let scrollHeight = document.documentElement.scrollHeight;
      if (scrollTop + clientHeight >= scrollHeight - 10 && !this.loading) {
        this.loading = true;
        setTimeout(() => {
          this.count += 20;
          this.axios
            .get(
              `https://apis.netstart.cn/douguo/recipe/search?keyword=${this.value}&order=${this.activeOrder}&type=${this.activeSecond}&offset=${this.count}&limit=20 `
            )
            .then((res) => {
              var idArr = [];
              this.searchList.map((item) => {
                idArr.push(item.r.id);
              });
              res.data.result.list.map((item) => {
                if (idArr.indexOf(item.r.id) == -1) {
                  this.searchList.push(item);
                } else {
                  return;
                }
              });
            });
          this.loading = false;
        }, 2000);
      }
    },
  },
  destroyed() {
    window.removeEventListener("scroll", this.onLoad, false);
  },
};
</script>

<style lang="less" scoped>
.searchcook {
  position: relative;
  .top {
    margin-top: 50px;
    .classification {
      border-bottom: 1px solid #c4c4c4;
      box-sizing: border-box;
      display: flex;
      text-align: center;
      justify-content: space-between;
      align-items: center;
      .left {
        display: flex;
        p {
          margin: 15px;
          &.active {
            font-weight: bold;
          }
        }
      }
    }
    .second {
      white-space: nowrap;
      overflow-x: auto;
      overflow-y: hidden;
      padding: 20px 10px;
      &::-webkit-scrollbar {
        display: none;
      }
      span {
        text-align: center;
        color: #999;
        background-color: #fafafa;
        box-sizing: border-box;
        padding: 8px 13px;
        margin: 5px;
        border-radius: 18px;
        &.active {
          background-color: #ffc533;
          color: #000;
          font-weight: bold;
        }
      }
    }
  }
  .loading {
    width: 100%;
    img {
      width: 25vw;
      display: block;
      margin: 20px auto;
      margin-bottom: 50px;
    }
  }
}
</style>
