<template>
  <div class="searchcard" @click="toCook" v-if="list">
    <div
      class="img"
      v-if="list.img"
      :style="{
        backgroundImage: 'url(' + list.img + ')',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
      }"
    >
      <span class="icon" v-if="list.vfurl"></span>
    </div>
    <p class="sketch">
      <span class="title">{{ list.n }}</span>
      <span class="score-learn">
        <span v-if="list.rate">{{ list.rate.toFixed(1) }}分</span>
        <span class="dot" v-if="list.rate && list.recommend_label"> • </span>
        <span v-if="list.recommend_label">{{ list.recommend_label }}</span>
      </span>
      <span class="author" @click.stop="toUser">
        <van-image width="20" height="20" round :src="list.a.p" />
        <span class="name">{{ list.a.n }}</span>
      </span>
    </p>
  </div>
</template>

<script>
export default {
  props: ["list"],
  data() {
    return {
      cookId: "",
    };
  },
  mounted() {
    this.cookId = this.list.id;
  },
  methods: {
    toCook: function () {
      this.$router.push({ path: "/details", query: { id: this.cookId } });
    },
    toUser: function () {
      this.$router.push({ path: "/user", query: { id: this.list.a.id } });
      this.$router.go(0);
    },
  },
};
</script>

<style lang="less" scoped>
.searchcard {
  padding: 10px 20px;
  width: 100vw;
  display: flex;
  align-items: center;
  .img {
    flex: 1;
    height: 120px;
    border-radius: 10px;
    position: relative;
    .icon {
      width: 50px;
      height: 50px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: url("../../assets/video.png");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }
  }
  .sketch {
    flex: 1;
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    & > span {
      margin: 2px 0;
    }
    .title {
      font-weight: bold;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    .score-learn {
      font-size: 14px;
      .dot {
        font-weight: bold;
      }
    }
    .author {
      display: flex;
      align-items: center;
      .name {
        font-size: 14px;
        margin-left: 5px;
        color: #c4c4c4;
      }
    }
  }
}
</style>
