<template>
  <div class="follow">
    <div class="msg_list">
      <Refresh :onRefresh="onRefresh">
        <div class="title">
          <p class="dgms">豆果美食，会做饭很酷~</p>
          <p>你关注人的动态会出现在这</p>
          <p>为你推荐了一些有趣的人，快关注 TA 们吧</p>
        </div>
        <FollowCard
          v-for="(list, index) in followList"
          :key="index"
          :list="list"
          :toggle-show-share="toggleShowShare"
        ></FollowCard>
        <div class="loading">
          <img src="../../assets/loading2.gif" />
        </div>
      </Refresh>
    </div>
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      get-container="#app"
      @select="onSelect"
    />
  </div>
</template>

<script>
import FollowCard from "@/components/Home/FollowCard.vue";
import Refresh from "@/components/Refresh.vue";
import { Toast } from "vant";
export default {
  components: {
    FollowCard,
    Refresh,
  },
  data() {
    return {
      followList: [],
      count: 0,
      loading: false,
      showShare: false,
      options: [
        [
          { name: "微信", icon: "wechat" },
          { name: "朋友圈", icon: "wechat-moments" },
          { name: "微博", icon: "weibo" },
          { name: "QQ", icon: "qq" },
        ],
        [
          { name: "复制链接", icon: "link" },
          { name: "分享海报", icon: "poster" },
          { name: "二维码", icon: "qrcode" },
          { name: "小程序码", icon: "weapp-qrcode" },
        ],
      ],
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onLoad);
    this.count = Math.floor(Math.random() * 100) * 10;
    this.axios
      .get(`https://apis.netstart.cn/douguo/home/ffeeds/${this.count}/10`)
      .then((res) => {
        this.followList = res.data.result.rfs;
      });
  },
  methods: {
    onRefresh(done) {
      this.count = Math.floor(Math.random() * 100) * 10;
      this.axios
        .get(`https://apis.netstart.cn/douguo/home/ffeeds/${this.count}/10`)
        .then((res) => {
          this.followList = res.data.result.rfs;
        });
      done();
    },
    onLoad() {
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      let clientHeight = document.documentElement.clientHeight;
      let scrollHeight = document.documentElement.scrollHeight;
      if (scrollTop + clientHeight >= scrollHeight - 10 && !this.loading) {
        this.loading = true;
        setTimeout(() => {
          this.count += 10;
          this.axios
            .get(`https://apis.netstart.cn/douguo/home/ffeeds/${this.count}/10`)
            .then((res) => {
              var idArr = [];
              this.followList.map((item) => {
                idArr.push(item.id);
              });
              res.data.result.rfs.map((item) => {
                if (idArr.indexOf(item.id) == -1) {
                  this.followList.push(item);
                } else {
                  return;
                }
              });
            });
          this.loading = false;
        }, 2000);
      }
    },
    onSelect(option) {
      Toast(option.name);
      this.showShare = false;
    },
    toggleShowShare: function () {
      this.showShare = true;
    },
  },
  destroyed() {
    window.removeEventListener("scroll", this.onLoad, false);
  },
};
</script>

<style lang="less" scoped>
.follow {
  width: 100vw;
  height: 100vh;
  overflow: auto;
  margin-top: 100px;
  .msg_list {
    margin-top: 5px;
    .title {
      padding: 10px 15px;
      font-size: 12px;
      color: #b6b6b6;
      background-color: #fafafa;
      z-index: 1;
      .dgms {
        font-size: 18px;
        color: #343434;
        font-weight: bold;
        margin-bottom: 5px;
      }
    }
    .loading {
      img {
        width: 20vw;
        display: block;
        margin: 0 auto;
        margin-bottom: 50px;
      }
    }
  }
}
</style>
