<template>
  <div class="note">
    <NoteCard
      v-for="(list, index) in noteList"
      :key="index"
      :list="list"
    ></NoteCard>
  </div>
</template>

<script>
import NoteCard from "@/components/User/NoteCard.vue";

export default {
  props: ["userId"],
  components: {
    NoteCard,
  },
  data() {
    return {
      noteList: [],
    };
  },
  mounted() {
    this.axios
      .get(
        `https://apis.netstart.cn/douguo/user/usernotescalendar/${this.userId}/0/20`
      )
      .then((res) => {
        this.noteList = res.data.result.notes;
      });
  },
};
</script>

<style lang="less" scoped></style>
