<template>
  <div class="contentcard" @click="toCook(video)" v-if="video.r">
    <div class="top" @click.stop="toUser(video)">
      <van-image
        class="header"
        width="25"
        height="25"
        round
        v-if="video.r.a"
        :src="getUserImg(video.r.a.p)"
      />
      <span class="name">{{ video.r.a.n }}</span>
      <i class="lv">Lv.{{ video.r.a.lvl }}</i>
    </div>
    <div class="content">
      <div class="img">
        <van-image width="100%" :height="imgHeight" :src="video.r.img" />
        <div class="videoImg" v-show="video.r.vfurl"></div>
      </div>
      <div class="collection">
        <van-image
          class="visitor"
          width="24"
          height="24"
          round
          :src="getUserImg(video.r.collect_users[0].p)"
        />
        <van-image
          class="visitor"
          width="24"
          height="24"
          round
          :src="getUserImg(video.r.collect_users[1].p)"
        />
        <van-image
          class="visitor"
          width="24"
          height="24"
          round
          :src="getUserImg(video.r.collect_users[2].p)"
        />
        <van-image
          class="visitor"
          width="24"
          height="24"
          round
          :src="getUserImg(video.r.collect_users[3].p)"
        />
        <p>{{ video.r.collect_count_text }}</p>
        <span class="unfav" v-if="!isFav" @click.stop="attentionThis"></span>
        <span class="fav" v-else @click.stop="cancelThis"></span>
      </div>
      <div class="introduction">{{ video.r.n }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["video"],
  data() {
    return {
      cookId: "",
      isFav: false,
      imgHeight: "",
    };
  },
  mounted() {
    if (this.video.r) {
      this.cookId = this.video.r.id;
      if (JSON.parse(localStorage.getItem("favCooks"))) {
        var favArr = JSON.parse(localStorage.getItem("favCooks"));
        if (favArr.indexOf(this.cookId) != -1) {
          this.isFav = true;
        }
      }
      this.imgHeight =
        (document.body.clientWidth / this.video.r.pw) * this.video.r.ph;
    }
  },
  methods: {
    getUserImg: function (src) {
      if (src != "") {
        return src;
      } else {
        return require("../../assets/user_no_photo.png");
      }
    },
    toCook: function (item) {
      this.$router.push({ path: "/details", query: { id: item.r.id } });
    },
    toUser: function (item) {
      this.$router.push({ path: "/user", query: { id: item.r.a.id } });
    },
    attentionThis: function () {
      this.isFav = true;
      var idArr = [];
      if (JSON.parse(localStorage.getItem("favCooks"))) {
        idArr = JSON.parse(localStorage.getItem("favCooks"));
      }
      if (idArr.indexOf(this.cookId) == -1) {
        idArr.push(this.cookId);
        localStorage.setItem("favCooks", JSON.stringify(idArr));
      }
    },
    cancelThis: function () {
      this.isFav = false;
      var idArr = [];
      idArr = JSON.parse(localStorage.getItem("favCooks"));
      idArr.splice(idArr.indexOf(this.cookId), 1);
      localStorage.setItem("favCooks", JSON.stringify(idArr));
    },
  },
};
</script>

<style lang="less" scoped>
.contentcard {
  .top {
    margin: 3vw 0vw 2vw 3vw;
    .header {
      vertical-align: middle;
    }
    .name {
      font-size: 12px;
      font-weight: bold;
      display: inline-block;
      transform: scale(0.9);
    }
    .lv {
      font-size: 12px;
      color: #ec8d19;
      display: inline-block;
      transform: scale(0.7);
    }
  }
  .content {
    .img {
      position: relative;
      img {
        width: 100vw;
        vertical-align: middle;
      }
      .videoImg {
        position: absolute;
        bottom: 10px;
        right: 10px;
        width: 35px;
        height: 35px;
        background: url("../../assets/video.png");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
      }
    }
    .collection {
      margin: 3vw;
      position: relative;
      .visitor {
        &:nth-child(2) {
          position: absolute;
          left: 12px;
        }
        &:nth-child(3) {
          position: absolute;
          left: 24px;
        }
        &:nth-child(4) {
          position: absolute;
          left: 36px;
        }
      }
      p {
        position: absolute;
        top: 0;
        left: 60px;
        line-height: 25px;
        font-size: 12px;
        font-weight: 700;
        transform: scale(0.8);
      }
      span {
        width: 5vw;
        height: 5vw;
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        &.unfav {
          background: url("../../assets/unfavorite.png");
          background-size: cover;
          background-position: center;
        }
        &.fav {
          background: url("../../assets/favorite.png");
          background-size: cover;
          background-position: center;
        }
      }
    }
    .introduction {
      font-size: 12px;
      transform: scale(0.95);
      margin-bottom: 3vw;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}
</style>
