<template>
  <div class="fav">
    <div class="mask" v-show="isMaskShow" ref="mask">
      <div class="masktop">
        <img
          class="icon"
          src="../assets/back_black.png"
          @click="toggleShowMask"
        />
        <p class="title">管理 菜谱</p>
      </div>
      <div class="content">
        <AdministrationCook
          :favCooks="favCooks"
          :checkCooks.sync="checkCooks"
        ></AdministrationCook>
      </div>
      <div class="maskbottom">
        <p class="cooksnum">已选中{{ checkCooks.length }}篇菜谱</p>
        <p
          class="clear"
          :class="{ active: checkCooks.length != 0 }"
          @click="clearChecked"
        >
          取消收藏
        </p>
      </div>
    </div>
    <!-- <van-tabs
      class="tabs"
      v-model="activeName"
      color="#ffb31a"
      padding="0"
      line-width="25px"
      animated
      swipeable
    > -->
    <!-- <van-tab title="收藏的菜" name="a"> -->
    <div class="top" v-if="favCooks.length != 0">
      <p class="administration" @click="toggleShowMask">管理</p>
    </div>
    <FavCook @showMask="toggleShowMask" :favCooks="favCooks"></FavCook>
    <!-- </van-tab>
      <van-tab title="关注的人" name="b">
        <FollowPeoples></FollowPeoples>
      </van-tab>
    </van-tabs> -->
  </div>
</template>
<script>
import FavCook from "@/components/Fav/FavCook.vue";
import AdministrationCook from "@/components/Fav/AdministrationCook.vue";
// import FollowPeoples from "@/components/Fav/FollowPeoples.vue";
import { Toast } from "vant";

export default {
  components: {
    FavCook,
    AdministrationCook,
    // FollowPeoples,
  },
  data() {
    return {
      favCooks: [],
      checkCooks: [],
      activeName: "a",
      isMaskShow: false,
    };
  },
  mounted() {
    if (JSON.parse(localStorage.getItem("favCooks"))) {
      this.favCooks = JSON.parse(localStorage.getItem("favCooks"));
    }
  },
  methods: {
    toggleShowMask: function () {
      this.isMaskShow = !this.isMaskShow;
    },
    clearChecked: function () {
      if (this.checkCooks.length != 0) {
        Toast("取消收藏成功");
        this.checkCooks.forEach((id) => {
          let index = this.favCooks.indexOf(id);
          if (index != -1) {
            this.favCooks.splice(index, 1);
          }
        });
        if (this.favCooks.length == 0) {
          localStorage.removeItem("favCooks");
        } else {
          localStorage.setItem("favCooks", JSON.stringify(this.favCooks));
        }
        this.checkCooks = [];
      }
    },
  },
};
</script>

