<template>
  <div class="search">
    <div class="top">
      <img class="back" src="../assets/back_black.png" @click="back" />
      <div class="searchbox">
        <img class="searchimg" src="../assets/search.png" />
        <input
          class="ipt"
          type="text"
          v-model.trim="value"
          :placeholder="randomPlaceholder"
          @input="getSuggests"
          @click="isResult = false"
          v-focus
        />
        <img
          class="clear"
          src="../assets/clear.png"
          v-if="value"
          @click="clear"
        />
      </div>
      <p @click="searchIt()">搜索</p>
    </div>
    <div class="suggests" v-if="value && !isResult">
      <ul>
        <li
          class="suggestlis"
          v-for="(suggest, index) in Suggests"
          :key="index"
          @click="searchIt(suggest)"
        >
          <p>
            <span
              v-for="(word, index) in suggest.suggests_names"
              :key="index"
              :style="{ fontWeight: word.is_bold ? 'bold' : 'normal' }"
              >{{ word.suggests_name }}</span
            >
          </p>
          <p
            class="tag"
            v-if="suggest.tag"
            :style="{
              color: suggest.tag.text_color,
              backgroundColor: suggest.tag.background_color,
              borderColor: suggest.tag.border_color,
            }"
          >
            {{ suggest.tag.name }}
          </p>
        </li>
      </ul>
    </div>
    <div class="search-result" v-else-if="value && isResult">
      <!-- <van-tabs
        class="tabs"
        v-model="activeName"
        color="#ffb31a"
        padding="0"
        line-width="25px"
        sticky
        offset-top="50px"
      > -->
      <!-- <van-tab title="菜谱" name="a"> -->
      <SearchCook :value="value"></SearchCook>
      <!-- </van-tab>
        <van-tab title="笔记" name="b"> </van-tab>
        <van-tab title="用户" name="c"> </van-tab>
        <van-tab title="商品" name="d"> </van-tab>
        <van-tab title="课堂" name="e"> </van-tab>
      </van-tabs> -->
    </div>
    <div class="blank" v-else>
      <div class="history" v-if="historyKeyWords.length != 0">
        <p class="title">
          <span>历史搜索</span>
          <span class="delete" @click="deleteHistory"></span>
        </p>
        <div class="historywords">
          <span
            v-for="(word, index) in historyKeyWords"
            :key="index"
            @click="searchIt(word)"
          >
            {{ word }}
          </span>
        </div>
      </div>
      <div class="hot">
        <p class="title">热门搜索</p>
        <div class="keywords">
          <span
            v-for="(keyword, index) in hotKeyWords"
            :key="index"
            :style="{
              color: keyword.color,
              backgroundColor: keyword.background_color,
              borderColor: keyword.borderColor,
            }"
            @click="searchIt(keyword.title)"
          >
            {{ keyword.title }}
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchCook from "@/components/Search/SearchCook.vue";

export default {
  components: {
    SearchCook,
  },
  data() {
    return {
      activeName: "a",
      historyKeyWords: [],
      hotKeyWords: [],
      value: "",
      randomPlaceholder: "",
      Suggests: [],
      isResult: false,
      searchWord: "",
      timeOut: null,
    };
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus();
      },
    },
  },
  mounted() {
    if (this.$store.state.classifysearch != "") {
      this.value = this.$store.state.classifysearch;
      if (
        JSON.parse(localStorage.getItem("historyKeyWords")) &&
        JSON.parse(localStorage.getItem("historyKeyWords")).length != 0
      ) {
        this.historyKeyWords = JSON.parse(
          localStorage.getItem("historyKeyWords")
        );
      }
      this.searchIt(this.value);
      this.$store.state.classifysearch = "";
    }

    this.axios
      .get("https://apis.netstart.cn/douguo/recipe/search/hot")
      .then((res) => {
        this.hotKeyWords = res.data.result.suggestdetails;
        this.randomPlaceholder =
          this.hotKeyWords[
            Math.floor(Math.random() * this.hotKeyWords.length)
          ].title;
      });
  },
  methods: {
    back: function () {
      this.$router.back();
    },
    clear: function () {
      this.value = "";
      this.isResult = false;
    },
    getSuggests: function (e) {
      clearTimeout(this.timeOut);
      this.timeOut = setTimeout(() => {
        this.searchWord = e.target.value;
        if (this.searchWord != "") {
          this.axios
            .get(
              "https://apis.netstart.cn/douguo/recipe/search/suggests?kw=" +
                this.searchWord +
                ""
            )
            .then((res) => {
              this.Suggests = res.data.result.suggests;
            });
        } else {
          this.searchList = "";
        }
      }, 1000);
    },
    searchIt: function (n) {
      if (n && n.trim != "") {
        if (n.name) {
          this.value = n.name;
        } else {
          this.value = n;
        }
      }
      this.searchWord = this.value;
      if (this.searchWord == "") {
        this.value = this.randomPlaceholder;
        this.searchWord = this.value;
      }
      this.isResult = true;
      var index = this.historyKeyWords.indexOf(this.searchWord);
      if (index == -1) {
        this.historyKeyWords.unshift(this.searchWord);
      } else {
        this.historyKeyWords.unshift(...this.historyKeyWords.splice(index, 1));
      }
      if (this.historyKeyWords.length > 10) {
        this.historyKeyWords.splice(10, this.historyKeyWords.length - 1);
        console.log(this.historyKeyWords);
      }
      localStorage.setItem(
        "historyKeyWords",
        JSON.stringify(this.historyKeyWords)
      );
    },
    deleteHistory: function () {
      this.historyKeyWords = [];
      localStorage.removeItem("historyKeyWords");
    },
  },
};
</script>

