<template>
  <div class="usermsg">
    <img src="../../assets/bg.png" />
    <div class="top" :class="{ active: showTop }">
      <div class="left">
        <img class="icon" src="../../assets/back_black.png" @click="back" />
        <div class="userimg" v-show="showTop" v-if="userMsg">
          <van-image class="header" :src="userMsg.user_photo" round />
          <span class="name">{{ userMsg.nick }}</span>
        </div>
      </div>
      <div class="right">
        <div v-show="showTop">
          <p class="follow" v-if="isFollow" @click="removeFollowThis">已关注</p>
          <p class="unfollow" v-else @click="addFollowThis">关注</p>
        </div>
        <img
          class="icon"
          src="../../assets/menu_more_black.png"
          @click="showShare = true"
        />
      </div>
    </div>
    <div class="content">
      <div class="msg">
        <van-image class="header" :src="userMsg.user_photo" round />
        <div class="userfav">
          <div>
            <p class="classname">关注</p>
            <p class="num">{{ userMsg.following_count }}</p>
          </div>
          <div>
            <p class="classname">粉丝</p>
            <p class="num">{{ userMsg.followers_count_text }}</p>
          </div>
          <div>
            <p class="classname">被收藏</p>
            <p class="num">{{ userMsg.favoed_count_text }}</p>
          </div>
          <div>
            <p class="classname">被学做</p>
            <p class="num">{{ userMsg.noted_count_text }}</p>
          </div>
        </div>
      </div>
      <div class="tag">
        <span v-if="userMsg.lvl">LV.{{ userMsg.lvl }}</span>
        <span v-if="userMsg.usermedal">
          {{ userMsg.usermedal.count_text }}勋章
        </span>
        <p v-if="userMsg.archivement_tags">
          <span
            v-for="(item, index) in userMsg.archivement_tags"
            :key="index"
            :style="{
              color: item.text_color,
              backgroundImage: `linear-gradient(to right ,${item.background_start_color}, ${item.background_end_color})`,
            }"
          >
            {{ item.text }}
          </span>
        </p>
      </div>
      <div class="name">
        <p>{{ userMsg.nick }}</p>
        <img v-if="userMsg.gender == 1" src="../../assets/male.png" />
        <img v-if="userMsg.gender == 2" src="../../assets/female.png" />
      </div>
      <div class="introduction">{{ userMsg.introduction }}</div>
      <div class="follow" v-if="isFollow" @click="removeFollowThis">已关注</div>
      <div class="unfollow" v-else @click="addFollowThis">关注</div>
      <div class="tabs">
        <p :class="{ active: activeName == 'a' }" @click="toggleActive('a')">
          菜谱 {{ userMsg.recipes_count }}
        </p>
        <p :class="{ active: activeName == 'b' }" @click="toggleActive('b')">
          笔记 {{ userMsg.notes_count }}
        </p>
        <p :class="{ active: activeName == 'c' }" @click="toggleActive('c')">
          收藏 {{ userMsg.collect_count }}
        </p>
      </div>
      <div class="details" v-if="userId">
        <KeepAlive>
          <Menu v-if="activeName == 'a'" :userId="userId"></Menu>
          <Note v-else-if="activeName == 'b'" :userId="userId"></Note>
          <Collection
            v-else-if="activeName == 'c'"
            :userId="userId"
          ></Collection>
        </KeepAlive>
      </div>
    </div>
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      @select="onSelect"
    />
  </div>
</template>

<script>
import { Toast } from "vant";
import Menu from "@/components/User/Menu.vue";
import Note from "@/components/User/Note.vue";
import Collection from "@/components/User/Collection.vue";

export default {
  components: {
    Menu,
    Note,
    Collection,
  },
  data() {
    return {
      userId: "",
      userMsg: [],
      isFollow: false,
      activeName: "a",
      showShare: false,
      options: [
        [
          { name: "微信", icon: "wechat" },
          { name: "朋友圈", icon: "wechat-moments" },
          { name: "微博", icon: "weibo" },
          { name: "QQ", icon: "qq" },
        ],
        [
          { name: "复制链接", icon: "link" },
          { name: "分享海报", icon: "poster" },
          { name: "二维码", icon: "qrcode" },
          { name: "小程序码", icon: "weapp-qrcode" },
        ],
      ],
      showTop: false,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.windowScroll, true);
    this.userId = this.$route.query.id;
    this.getMsg();
  },
  methods: {
    getMsg: function () {
      this.axios
        .get("https://apis.netstart.cn/douguo/user/info/" + this.userId + "")
        .then((res) => {
          this.userMsg = res.data.result.user;
        });
      if (JSON.parse(localStorage.getItem("FollowPeoples"))) {
        var followArr = JSON.parse(localStorage.getItem("FollowPeoples"));
        if (followArr.indexOf(this.userId) != -1) {
          this.isFollow = true;
        }
      }
    },
    back: function () {
      this.$router.back();
    },
    toggleActive: function (n) {
      this.activeName = n;
    },
    onSelect(option) {
      Toast(option.name);
      this.showShare = false;
    },
    windowScroll(e) {
      if (e.target.scrollTop <= 40) {
        this.showTop = false;
      } else {
        this.showTop = true;
      }
    },
    addFollowThis: function () {
      this.isFollow = true;
      var idArr = [];
      if (JSON.parse(localStorage.getItem("FollowPeoples"))) {
        idArr = JSON.parse(localStorage.getItem("FollowPeoples"));
      }
      if (idArr.indexOf(this.userId) == -1) {
        idArr.unshift(this.userId);
        localStorage.setItem("FollowPeoples", JSON.stringify(idArr));
      }
    },
    removeFollowThis: function () {
      this.isFollow = false;
      var idArr = [];
      idArr = JSON.parse(localStorage.getItem("FollowPeoples"));
      idArr.splice(idArr.indexOf(this.userId), 1);
      localStorage.setItem("FollowPeoples", JSON.stringify(idArr));
    },
  },
  watch: {
    $route: function (to) {
      this.userId = to.query.id;
      this.getMsg();
    },
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.windowScroll, false);
  },
};
</script>

<style lang="less" scoped>
.usermsg {
  width: 100vw;
  height: 100vh;
  position: relative;
  .bg {
    position: absolute;
    top: 0;
    z-index: -1;
  }
  .top {
    width: 100vw;
    height: 70px;
    padding: 0 10px;
    position: fixed;
    top: 0;
    z-index: 2;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .icon {
      width: 25px;
      height: 25px;
    }
    &.active {
      background-color: #fff;
    }
    .left {
      display: flex;
      align-items: center;
      .userimg {
        display: flex;
        align-items: center;
        .header {
          width: 35px;
          height: 35px;
        }
        .name {
          margin-left: 5px;
          font-size: 22px;
        }
      }
    }
    .right {
      display: flex;
      align-items: center;
      font-size: 14px;
      font-weight: bold;
      .unfollow {
        margin-right: 10px;
        padding: 8px 25px;
        border-radius: 20px;
        background-color: #ffc533;
      }
      .follow {
        margin-right: 10px;
        padding: 8px 18px;
        border-radius: 20px;
        background-color: #f5f5f5;
      }
    }
  }
  .content {
    width: 100vw;
    height: 1000px;
    background-color: #fff;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    position: absolute;
    top: 100px;
    z-index: 1;
    .msg {
      height: 80px;
      position: relative;
      .header {
        width: 25vw;
        height: 25vw;
        border: 2px solid #fff;
        box-sizing: border-box;
        position: absolute;
        top: -6vw;
        left: 6vw;
      }
      .userfav {
        display: flex;
        position: absolute;
        left: 36vw;
        div {
          padding: 0 10px;
          margin-top: 18px;
          .classname {
            font-size: 12px;
            color: #c4c4c4;
          }
          .num {
            padding-top: 5px;
            line-height: 25px;
            font-weight: bold;
          }
        }
      }
    }
    .tag {
      margin-top: 20px;
      margin-left: 8vw;
      span {
        background-color: #fbf7ec;
        border-radius: 3px;
        padding: 5px;
        margin: 0 3px;
        font-size: 12px;
        font-weight: bold;
      }
      p {
        display: inline-block;
      }
    }
    .name {
      margin-top: 10px;
      margin-left: 6vw;
      display: flex;
      align-items: center;
      p {
        font-size: 28px;
        font-weight: bold;
      }
      img {
        margin-top: 5px;
        margin-left: 5px;
        width: 20px;
        height: 20px;
        vertical-align: middle;
      }
    }
    .introduction {
      margin: 5px 6vw;
      color: #c4c4c4;
    }
    .unfollow {
      margin: 5px 6vw;
      margin-bottom: 20px;
      width: 88vw;
      padding: 10px 0;
      text-align: center;
      border-radius: 20px;
      background-color: #ffc533;
    }
    .follow {
      margin: 5px 6vw;
      margin-bottom: 20px;
      width: 88vw;
      padding: 10px 0;
      text-align: center;
      border-radius: 20px;
      background-color: #fafafa;
    }
    .tabs {
      padding-top: 10px;
      border-top: 10px solid #fafafa;
      display: flex;
      margin: 0 6vw;
      justify-content: space-evenly;
      p {
        color: #c4c4c4;
        &.active {
          color: #2c3e50;
          &::after {
            content: "";
            display: block;
            width: 25px;
            height: 3px;
            margin-top: 10px;
            margin-left: 30%;
            background-color: #ffc533;
          }
        }
      }
    }
  }
}
</style>
