<template>
  <div class="followcard" @click="toDetails(list)" v-if="list">
    <div class="top">
      <div class="username" @click.stop="toUser(list)">
        <van-image
          class="header"
          width="25"
          height="25"
          round
          :src="getUserImg(list.u.p)"
        />
        <div class="nameLv">
          <span class="name">{{ list.u.n }}</span>
          <i class="lv">Lv.{{ list.u.lvl }}</i>
        </div>
        <div class="fans">
          {{ list.u.followers_count_text }} · {{ list.u.recipes_count_text }}
        </div>
      </div>
      <div class="unfocus" v-if="!isFollow" @click.stop="addFollowThis">
        关注
      </div>
      <div class="focus" v-else @click.stop="removeFollowThis">已关注</div>
    </div>
    <div class="content">
      <div class="imgbox">
        <van-image
          width="100%"
          :height="imgHeight"
          fit="cover"
          :src="list.img"
        />
      </div>
      <div class="collection">
        <div>
          <van-image
            class="otheruser"
            width="24"
            height="24"
            round
            v-for="(item, index) in list.collect_users"
            :key="index"
            :src="getUserImg(item.p)"
          />
          <div class="videoImg" v-show="list.video_url"></div>
          <p>{{ list.collect_count_text }}</p>
        </div>
        <div class="icon">
          <img
            class="unfav"
            v-if="!isFav"
            src="../../assets/unfavorite.png"
            @click.stop="addFavThis"
          />
          <img
            class="fav"
            v-else
            src="../../assets/favorite.png"
            @click.stop="removeFavThis"
          />
          <!-- <img class="comment" src="../../assets/comment.png" /> -->
          <img
            class="share"
            src="../../assets/share.png"
            @click.stop="toggleShowShare()"
          />
        </div>
      </div>
      <div class="introduction">{{ list.n }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["list", "toggleShowShare"],
  data() {
    return {
      cookId: "",
      peopleId: "",
      isFav: false,
      isFollow: false,
      imgHeight: "",
    };
  },
  mounted() {
    this.imgHeight = (document.body.clientWidth / this.list.pw) * this.list.ph;
    this.cookId = this.list.item_id;
    if (JSON.parse(localStorage.getItem("favCooks"))) {
      var favArr = JSON.parse(localStorage.getItem("favCooks"));
      if (favArr.indexOf(this.cookId) != -1) {
        this.isFav = true;
      }
    }
    this.peopleId = this.list.u.id;
    if (JSON.parse(localStorage.getItem("FollowPeoples"))) {
      var followArr = JSON.parse(localStorage.getItem("FollowPeoples"));
      if (followArr.indexOf(this.peopleId) != -1) {
        this.isFollow = true;
      }
    }
  },
  methods: {
    getUserImg: function (src) {
      if (src != "") {
        return src;
      } else {
        return require("../../assets/user_no_photo.png");
      }
    },
    toDetails: function (item) {
      this.$router.push({ path: "/details", query: { id: item.item_id } });
    },
    toUser: function (item) {
      this.$router.push({ path: "/user", query: { id: item.u.id } });
    },
    addFavThis: function () {
      this.isFav = true;
      var idArr = [];
      if (JSON.parse(localStorage.getItem("favCooks"))) {
        idArr = JSON.parse(localStorage.getItem("favCooks"));
      }
      if (idArr.indexOf(this.cookId) == -1) {
        idArr.unshift(this.cookId);
        localStorage.setItem("favCooks", JSON.stringify(idArr));
      }
    },
    removeFavThis: function () {
      this.isFav = false;
      var idArr = [];
      idArr = JSON.parse(localStorage.getItem("favCooks"));
      idArr.splice(idArr.indexOf(this.cookId), 1);
      localStorage.setItem("favCooks", JSON.stringify(idArr));
    },
    addFollowThis: function () {
      this.isFollow = true;
      var idArr = [];
      if (JSON.parse(localStorage.getItem("FollowPeoples"))) {
        idArr = JSON.parse(localStorage.getItem("FollowPeoples"));
      }
      if (idArr.indexOf(this.peopleId) == -1) {
        idArr.unshift(this.peopleId);
        localStorage.setItem("FollowPeoples", JSON.stringify(idArr));
      }
    },
    removeFollowThis: function () {
      this.isFollow = false;
      var idArr = [];
      idArr = JSON.parse(localStorage.getItem("FollowPeoples"));
      idArr.splice(idArr.indexOf(this.peopleId), 1);
      localStorage.setItem("FollowPeoples", JSON.stringify(idArr));
    },
  },
};
</script>

<style lang="less" scoped>
.followcard {
  .top {
    margin: 3vw 0vw 15px 3vw;
    display: flex;
    justify-content: space-between;
    .username {
      flex: 1;
      position: relative;
      .header {
        position: absolute;
        vertical-align: middle;
      }
      .nameLv {
        width: 50vw;
        height: 30px;
        position: absolute;
        top: 0;
        left: 27px;
        .name {
          font-size: 12px;
          font-weight: bold;
          display: inline-block;
          transform: scale(0.9);
        }
        .lv {
          font-size: 12px;
          color: #ec8d19;
          display: inline-block;
          transform: scale(0.7);
        }
      }
      .fans {
        font-size: 12px;
        color: #ccc;
        position: absolute;
        top: 15px;
        transform: scale(0.8) translateX(25px);
      }
    }
    .unfocus {
      margin-right: 5px;
      font-size: 12px;
      font-weight: bold;
      padding: 3px 15px;
      border-radius: 15px;
      background-color: #ffc533;
    }
    .focus {
      margin-right: 5px;
      font-size: 12px;
      font-weight: bold;
      padding: 3px 9px;
      border-radius: 15px;
      background-color: #f5f5f5;
    }
  }
  .content {
    .imgbox {
      position: relative;
      .videoImg {
        position: absolute;
        bottom: 50px;
        right: 10px;
        width: 35px;
        height: 35px;
        background: url("../../assets/video.png");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
      }
    }
    .collection {
      margin: 3vw;
      position: relative;
      display: flex;
      img {
        width: 24px;
        height: 24px;
      }
      .otheruser {
        border-radius: 50%;
        &:nth-child(2) {
          position: absolute;
          left: 12px;
        }
        &:nth-child(3) {
          position: absolute;
          left: 24px;
        }
        &:nth-child(4) {
          position: absolute;
          left: 36px;
        }
      }
      p {
        position: absolute;
        top: 0;
        left: 60px;
        line-height: 25px;
        font-size: 12px;
        font-weight: 700;
        transform: scale(0.8);
      }
      .icon {
        display: flex;
        align-items: center;
        .unfav {
          position: absolute;
          right: 40px;
        }
        .fav {
          position: absolute;
          right: 40px;
        }
        // .unfav {
        //   position: absolute;
        //   right: 80px;
        // }
        // .fav {
        //   position: absolute;
        //   right: 80px;
        // }
        // .comment {
        //   position: absolute;
        //   right: 40px;
        // }
        .share {
          position: absolute;
          right: 0px;
        }
      }
    }
  }
  .introduction {
    font-size: 12px;
    transform: scale(0.95);
    margin-bottom: 3vw;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
</style>
