<template>
  <div class="favcookcard" v-if="cookList" @click="toDetails">
    <div
      class="img"
      v-if="cookList.photo_path"
      :style="{
        backgroundImage: 'url(' + cookList.photo_path + ')',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
      }"
    >
      <span class="icon" v-if="cookList.vfurl"></span>
    </div>
    <p class="sketch">
      <span class="title">{{ cookList.title }}</span>
      <span class="look-learn">
        <span v-if="cookList.views_count_text"
          >{{ cookList.views_count_text }}人看过</span
        >
        <span
          class="dot"
          v-if="cookList.views_count_text && cookList.dish_count_text"
        >
          •
        </span>
        <span v-if="cookList.dish_count_text"
          >{{ cookList.dish_count_text }}人学做</span
        >
      </span>
      <span class="author">
        <van-image
          width="20"
          height="20"
          round
          :src="cookList.user.user_photo"
        />
        <span class="name">{{ cookList.user.nick }}</span>
      </span>
    </p>
  </div>
</template>

<script>
export default {
  props: ["cookId"],
  data() {
    return {
      cookList: "",
    };
  },
  mounted() {
    this.axios
      .get("https://apis.netstart.cn/douguo/recipe/detail/" + this.cookId + "")
      .then((res) => {
        this.cookList = res.data.result.recipe;
      });
  },
  methods: {
    toDetails: function () {
      this.$router.push({ path: "/details", query: { id: this.cookId } });
    },
  },
};
</script>

<style lang="less" scoped>
.favcookcard {
  padding: 10px 20px;
  width: 100vw;
  display: flex;
  align-items: center;
  .img {
    flex: 1;
    height: 120px;
    border-radius: 10px;
    position: relative;
    .icon {
      width: 50px;
      height: 50px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: url("../../assets/video.png");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }
  }
  .sketch {
    flex: 1;
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    & > span {
      margin: 2px 0;
    }
    .title {
      font-weight: bold;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    .look-learn {
      font-size: 14px;
      .dot {
        font-weight: bold;
      }
    }
    .author {
      display: flex;
      align-items: center;
      margin-top: 10px;
      .name {
        font-size: 14px;
        margin-left: 5px;
        color: #c4c4c4;
      }
    }
  }
}
</style>
