<template>
  <div class="note-details">
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      @select="onSelect"
    />
    <div class="topbar">
      <div class="left">
        <img class="icon" src="../../assets/back_black.png" @click="back" />
        <div class="user" @click.stop="toUser">
          <van-image
            class="header"
            width="40"
            height="40"
            round
            :src="author.p"
          />
          <div class="user-detail">
            <p class="name-lv">
              <span class="name">{{ author.n }}</span>
              <i class="lv">LV.{{ author.lvl }}</i>
            </p>
            <p class="straight" v-if="note.straight_text">
              {{ note.straight_text }}
            </p>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="unfocus" v-if="!isFollow" @click.stop="addFollowThis">
          关注
        </div>
        <div class="focus" v-else @click.stop="removeFollowThis">已关注</div>
        <img
          class="icon"
          src="../../assets/menu_more_black.png"
          @click="showShare = true"
        />
      </div>
    </div>
    <div class="mask" v-show="isShowMask"></div>
    <div class="content">
      <div class="topimg">
        <div class="images" v-if="!note.video_url" :onload="loaded()">
          <van-swipe @change="onChange">
            <van-swipe-item v-for="(image, index) in topImages" :key="index">
              <img v-lazy="image" />
            </van-swipe-item>
            <template #indicator>
              <div class="custom-indicator">
                {{ current + 1 }}/{{ topImages.length }}
              </div>
            </template>
          </van-swipe>
          <div class="icon" v-if="note.selection_text">
            {{ note.selection_text }}
          </div>
        </div>
        <div class="video" v-else :onload="loaded()">
          <video
            :src="note.video_url"
            controls
            muted
            style="width: 100%"
          ></video>
          <div class="icon" v-if="note.selection_text">
            {{ note.selection_text }}
          </div>
        </div>
      </div>
      <div class="container">
        <p class="title">{{ note.title }}</p>
        <p class="notecontent">{{ contents }}</p>
        <p class="time-like">
          <span class="date">{{ update }}</span>
          <span class="like" v-if="note.like_count_text != 0"
            >{{ note.like_count_text }}次赞</span
          >
        </p>
        <div class="comment">
          <p class="title" v-show="comments.length != 0">笔记评论</p>
          <Comment
            v-for="(comment, index) in comments"
            :key="index"
            :comment="comment"
            :author="author.id"
          ></Comment>
        </div>
        <div class="relevant">
          <p class="title">相关笔记</p>
          <waterfall :col="2" :data="noteLists">
            <NoteCard
              v-for="(list, index) in noteLists"
              :key="index"
              :list="list"
            ></NoteCard>
          </waterfall>
          <!-- <div class="loading">
            <img src="../../assets/loading2.gif" />
          </div> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import Comment from "@/components/Home/Comment.vue";
import NoteCard from "@/components/Home/NoteCard.vue";

export default {
  components: {
    Comment,
    NoteCard,
  },
  data() {
    return {
      id: "",
      note: [],
      author: [],
      isShowMask: true,
      isFollow: false,
      topImages: [],
      current: 0,
      contents: "",
      update: "",
      comments: [],
      count: 0,
      noteLists: [],
      showShare: false,
      options: [
        [
          { name: "微信", icon: "wechat" },
          { name: "朋友圈", icon: "wechat-moments" },
          { name: "微博", icon: "weibo" },
          { name: "QQ", icon: "qq" },
        ],
        [
          { name: "复制链接", icon: "link" },
          { name: "分享海报", icon: "poster" },
          { name: "二维码", icon: "qrcode" },
          { name: "小程序码", icon: "weapp-qrcode" },
        ],
      ],
      loading: true,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onLoad, true);
    this.id = this.$route.query.id;
    this.loading = false;
    this.getMsg();
    this.$toast({
      message: "加载中...",
      icon: require("../../assets/loading5.gif"),
      forbidClick: true,
      overlay: true,
      duration: 3000,
    });
  },
  methods: {
    getMsg: function () {
      if (this.id != "") {
        this.axios
          .get("https://apis.netstart.cn/douguo/note/detail/" + this.id + "")
          .then((res) => {
            this.note = res.data.result.note;
            this.author = this.note.author;
            this.note.images.map((img) => {
              this.topImages.push(img.u);
            });
            if (this.note.contents[0].type == 0) {
              this.contents = this.note.contents[0].c;
            }
            //todo发布时间
            this.update = this.note.publishtime.split(" ")[0];
            this.comments = this.note.cs;
          });
        this.axios
          .get(
            "https://apis.netstart.cn/douguo/note/related/" +
              this.id +
              "/" +
              this.count +
              "/20"
          )
          .then((res) => {
            res.data.result.list.map((item) => {
              this.noteLists.push(item);
            });
          });
      }
    },
    loaded: function () {
      setTimeout(() => {
        this.isShowMask = false;
      }, 3000);
    },
    back: function () {
      this.$router.back();
    },
    onSelect(option) {
      Toast(option.name);
      this.showShare = false;
    },
    onChange(index) {
      this.current = index;
    },
    // onLoad() {
    //   let scrollTop =
    //     document.documentElement.scrollTop || document.body.scrollTop;
    //   let clientHeight = document.documentElement.clientHeight;
    //   let scrollHeight = document.documentElement.scrollHeight;
    //   if (scrollTop + clientHeight >= scrollHeight - 10 && !this.loading) {
    //     this.loading = true;
    //     setTimeout(() => {
    //       this.count += 20;
    //       this.axios
    //         .get(
    //           `https://apis.netstart.cn/douguo/note/related/${this.id}/${this.count}/20 `
    //         )
    //         .then((res) => {
    //           var idArr = [];
    //           this.noteLists.map((item) => {
    //             idArr.push(item.note ? item.note.id : item.activity.id);
    //           });
    //           res.data.result.list.map((item) => {
    //             if (idArr.indexOf(item.note.id) == -1) {
    //               this.noteLists.push(item);
    //             } else {
    //               return;
    //             }
    //           });
    //         });
    //       this.loading = false;
    //     }, 2000);
    //   }
    // },
    addFollowThis: function () {
      this.isFollow = true;
      var idArr = [];
      if (JSON.parse(localStorage.getItem("FollowPeoples"))) {
        idArr = JSON.parse(localStorage.getItem("FollowPeoples"));
      }
      if (idArr.indexOf(this.author.id) == -1) {
        idArr.unshift(this.author.id);
        localStorage.setItem("FollowPeoples", JSON.stringify(idArr));
      }
    },
    removeFollowThis: function () {
      this.isFollow = false;
      var idArr = [];
      idArr = JSON.parse(localStorage.getItem("FollowPeoples"));
      idArr.splice(idArr.indexOf(this.author.id), 1);
      localStorage.setItem("FollowPeoples", JSON.stringify(idArr));
    },
    toUser: function () {
      this.$router.push({ path: "/user", query: { id: this.author.id } });
    },
  },
  watch: {
    $route: function (to) {
      this.id = to.query.id;
      this.getMsg();
    },
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.onLoad, false);
  },
};
</script>

<style lang="less" scoped>
.note-details {
  width: 100vw;
  height: 100%;
  position: absolute;
  z-index: 2;
  background-color: #fff;
  overflow-y: auto;
  .topbar {
    width: 100%;
    height: 70px;
    background: #fff;
    position: fixed;
    top: 0;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    .icon {
      width: 30px;
      height: 30px;
    }
    .left {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .user {
        margin-left: 10px;
        display: flex;
        align-items: center;
        font-size: 14px;
        .user-detail {
          display: flex;
          flex-direction: column;
          margin-left: 5px;
          .name-lv {
            font-weight: bold;
            width: 35vw;
            display: flex;
            .name {
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            .lv {
              margin-left: 5px;
              margin-top: 2px;
              color: #ec8d19;
            }
          }
          .straight {
            margin-top: 5px;
            color: #c4c4c4;
          }
        }
      }
    }
    .right {
      display: flex;
      align-items: center;
      .unfocus {
        margin-right: 15px;
        font-size: 14px;
        font-weight: bold;
        padding: 5px 20px;
        border-radius: 25px;
        background-color: #ffc533;
      }
      .focus {
        margin-right: 15px;
        font-size: 14px;
        font-weight: bold;
        padding: 5px 13px;
        border-radius: 25px;
        background-color: #f5f5f5;
      }
    }
  }
  .mask {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: 2;
  }
  .content {
    .topimg {
      width: 100%;
      margin-top: 70px;
      .images {
        position: relative;
        .icon {
          display: inline-block;
          background-color: #ffc533;
          font-size: 14px;
          font-weight: bold;
          padding: 3px 7px;
          border-radius: 5px;
          position: absolute;
          left: 20px;
          bottom: 20px;
        }
        .custom-indicator {
          position: absolute;
          right: 5px;
          bottom: 5px;
          padding: 2px 5px;
          font-size: 12px;
          background: rgba(0, 0, 0, 0.1);
        }
      }
      .video {
        width: 100%;
        position: relative;
        .icon {
          display: inline-block;
          background-color: #ffc533;
          font-size: 14px;
          font-weight: bold;
          padding: 3px 7px;
          border-radius: 5px;
          position: absolute;
          left: 20px;
          bottom: -35px;
        }
      }
    }
    .container {
      margin: 5vw;
      width: 90vw;
      margin-top: 50px;
      .title {
        font-size: 20px;
        font-weight: bold;
      }
      .notecontent {
        margin-top: 30px;
      }
      .time-like {
        margin-top: 50px;
        font-size: 12px;
        color: #c4c4c4;
        display: flex;
        justify-content: space-between;
      }
      .comment {
        margin-top: 30px;
      }
      .relevant {
        margin-top: 30px;
        // .loading {
        //   width: 100%;
        //   img {
        //     width: 20vw;
        //     display: block;
        //     margin: 0 auto;
        //     margin-bottom: 50px;
        //   }
        // }
      }
    }
  }
}
</style>
