<template>
  <div class="videos">
    <div class="msg_list">
      <Refresh :onRefresh="onRefresh">
        <VideosCard
          v-for="(video, index) in videosMsg"
          :key="index"
          :video="video"
        ></VideosCard>
        <div class="loading">
          <img src="../../assets/loading2.gif" />
        </div>
      </Refresh>
    </div>
  </div>
</template>

<script>
import Refresh from "@/components/Refresh.vue";
import VideosCard from "@/components/Home/VideosCard.vue";
export default {
  components: {
    Refresh,
    VideosCard,
  },
  data() {
    return {
      videosMsg: [],
      count: 0,
      loading: false,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onLoad, true);
    this.count = Math.floor(Math.random() * 100) * 10;
    this.axios
      .get(`https://apis.netstart.cn/douguo/home/videos/${this.count}/20`)
      .then((res) => {
        this.videosMsg = res.data.result.list;
      });
  },
  methods: {
    onRefresh(done) {
      this.videosMsg = [];
      this.count = Math.floor(Math.random() * 100) * 10;
      this.axios
        .get(`https://apis.netstart.cn/douguo/home/videos/${this.count}/10`)
        .then((res) => {
          this.videosMsg = res.data.result.list.filter((item) => {
            return item.type !== 128;
          });
        });
      done();
    },
    onLoad() {
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      let clientHeight = document.documentElement.clientHeight;
      let scrollHeight = document.documentElement.scrollHeight;
      if (scrollTop + clientHeight >= scrollHeight - 10 && !this.loading) {
        this.loading = true;
        setTimeout(() => {
          this.count += 10;
          this.axios
            .get(
              `https://apis.netstart.cn/douguo/home/videos/${this.count}/10`
            )
            .then((res) => {
              var idArr = [];
              var MidMsgs = [];
              this.videosMsg.map((item) => {
                idArr.push(item.r.id);
              });
              MidMsgs = res.data.result.list.filter((item) => {
                return item.type !== 128;
              });
              MidMsgs.map((item) => {
                if (idArr.indexOf(item.r.id) == -1) {
                  this.videosMsg.push(item);
                } else {
                  return;
                }
              });
            });
          this.loading = false;
        }, 2000);
      }
    },
  },
  destroyed() {
    window.removeEventListener("scroll", this.onLoad, false);
  },
};
</script>

<style lang="less" scoped>
.videos {
  width: 100vw;
  height: 100vh;
  overflow: auto;
  margin-top: 100px;
  .msg_list {
    /deep/ .van-swipe__indicator {
      width: 12px;
      height: 5px;
      transform: scaleY(0.5);
      border-radius: 20%;
      background-color: #2c3e50;
    }
    /deep/ .van-swipe__indicator--active {
      background-color: #efe;
    }
    .loading {
      margin-bottom: 50px;
      img {
        width: 20vw;
        display: block;
        margin: 0 auto;
        margin-bottom: 50px;
      }
    }
  }
}
</style>
