<template>
  <div
    class="notecard"
    v-if="list.activity || list.note"
    @click="toDetails(list)"
  >
    <img :src="list.activity ? list.activity.image : list.note.image_u" />
    <img class="icon" v-if="list.note.video_url" src="../../assets/video.png" />
    <img class="icon" v-else src="../../assets/note_images.png" />
    <div class="straight" v-if="list.note">
      <span v-if="list.note.straight_text != ''">{{
        list.note.straight_text
      }}</span>
    </div>
    <p class="name">
      {{ list.activity ? list.activity.name : list.note.title }}
    </p>
    <div class="bottom">
      <div class="author" @click.stop="toUser(list)">
        <img
          :src="
            list.activity
              ? list.activity.author.p
              : getUserImg(list.note.author.p)
          "
        />
        <span>{{
          list.activity ? list.activity.author.n : list.note.author.n
        }}</span>
      </div>
      <div class="assist">
        <img v-if="list.activity" :src="list.activity.action_icon" />
        <img
          v-else-if="!isLike"
          src="../../assets/unlike.png"
          @click.stop="toggleLike"
        />
        <img v-else src="../../assets/like.png" @click.stop="toggleLike" />
        <span v-if="list.activity">{{ list.activity.action_title }}</span>
        <span @click.stop="toggleLike" v-else-if="!list.activity && !isLike">{{
          list.note.like_count != 0 ? list.note.like_count : "赞"
        }}</span>
        <span class="like" @click.stop="toggleLike" v-else>{{
          list.note.like_count != 0 ? list.note.like_count + 1 : 1
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["list"],
  data() {
    return {
      isLike: false,
    };
  },
  mounted() {
    this.list.note.like_state == 0
      ? (this.isLike = false)
      : (this.isLike = true);
  },
  methods: {
    getUserImg: function (src) {
      if (src != "") {
        return src;
      } else {
        return require("../../assets/user_no_photo.png");
      }
    },
    toDetails: function (item) {
      if (item.type == 1) {
        this.$router.push({ path: "/notedetail", query: { id: item.note.id } });
        this.$router.go(0);
      }
    },
    toggleLike: function () {
      this.isLike = !this.isLike;
    },
    toUser: function (item) {
      this.$router.push({ path: "/user", query: { id: item.note.author.id} });
    },
  },
};
</script>

<style lang="less" scoped>
.notecard {
  width: 98%;
  margin: 1%;
  background-color: #fafafa;
  position: relative;
  img {
    width: 100%;
    border-radius: 1vw;
  }
  .icon {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 10px;
    right: 10px;
  }
  .straight {
    margin-bottom: 1vw;
    span {
      font-size: 12px;
      margin-left: 3.5vw;
      position: relative;
      &::before {
        content: "";
        display: inline-block;
        width: 13px;
        height: 13px;
        position: absolute;
        top: 0px;
        left: -14px;
        background: url("../../assets/straight.png");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        vertical-align: middle;
      }
    }
  }
  .name {
    margin-left: 1vw;
    font-size: 12px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  .bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .author {
      margin-left: 1vw;
      margin-top: 2vw;
      position: relative;
      img {
        width: 15px;
        border-radius: 50%;
      }
      span {
        font-size: 12px;
        width: 30vw;
        color: #888;
        position: absolute;
        transform: scale(0.9) translateX(-5px);
      }
    }
    .assist {
      width: 5vw;
      margin-right: 15px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      img {
        width: 12px;
        border-radius: 0%;
        vertical-align: middle;
      }
      span {
        font-size: 16px;
        color: #888;
        display: inline-block;
        transform: scale(0.7);
        &.like {
          color: #e2493d;
        }
      }
    }
  }
}
</style>
